.about {

	padding: 1rem;
	position: relative;

	h2, h3 {
		font-family: "LeagueSpartan", 'serif';
	}

	h2 {
		font-size: 1.7rem;
	}

	h3 {
		text-transform: uppercase;
		font-size: 1rem;
		letter-spacing: 0.1rem;
	}

	p {
		font-family: "Montserrat", "sans-serif";
	}

}

.graphics::after {

	content: '';
	display: block;
	position: absolute;
	bottom: -1rem;
	right: 0;
	width: 73%;
	height: 200px;
	background-color: lighten(#171716,85%);
	z-index: -100;

}
